.page--detail-card {
  padding: 24px;
  box-sizing: border-box;
}

.detail-card {
  height: 100%;
  // width: 100%;
  // background: #e7e7e7;

  &__header {
    .row {
      &-01 {
        color: #999;

        .spacer {
          margin: 0 10px;
        }
      }

      &-02 {
        color: #383e47;
        margin-top: 14px;
        padding: 0 0 18px;
      }

      &-03 {
        padding-top: 4px;
        font-size: 12px;
        color: #999;

        .spacer {
          margin: 0 20px;

          &:last-child {
            display: none;
          }
        }
      }
    }
  }

  &__card {
    box-sizing: border-box;
    background: #fff;

    .title {
      font-size: 16px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0 24px 12px;
      margin: -12px -24px 0 -24px;

      &:not(:first-child) {
        margin-top: 32px;
      }
    }

    ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
    }

    li {
      display: flex;
      margin-top: 8px;
    }

    &--base {
      width: calc(100% - 384px - 24px);

      li {
        width: 50%;

        div {
          &:first-child {
            width: 110px;
            height: 100%;
            flex-shrink: 0;
            color: #999;
          }

          &:last-child {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: keep-all;
            width: calc(100% - 150px);
            line-height: 22px;
          }
        }
      }
    }

    &--record {
      width: 384px;
      flex-shrink: 0;
      margin-left: 24px;

      .hi-timeline {
        margin-top: 24px;
      }
    }

    &--stepper {
      margin-top: 24px;

      .stepper {
        padding-top: 24px;
      }

      .form-title {
        font-size: 16px;
        // text-align: center;
        justify-content: center;
        padding: 24px 0 12px;
      }
    }
  }
}
