.layout--card .layout__main {
  background-color: #f6f6f6; }

.page--gutter {
  padding: 24px; }

.page--gutter--horizontal {
  padding-left: 24px;
  padding-right: 24px; }

.page--gutter--vertical {
  padding-top: 24px;
  padding-bottom: 24px; }

.page--toolbar--fixed .block-filter__actions {
  position: fixed;
  top: 16px;
  right: 16px;
  z-index: 12; }

.page .block-filter__label {
  width: 108px;
  box-sizing: border-box;
  position: relative;
  padding: 0 20px 0 20px;
  height: 32px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }
  .page .block-filter__label::after {
    content: '';
    position: absolute;
    top: 9px;
    right: 0;
    width: 1px;
    height: 14px;
    background: #d8d8d8; }
  .page .block-filter__label--radio {
    margin-right: 20px;
    line-height: 32px; }
  .page .block-filter__label--checkbox {
    margin-right: 8px; }
    .page .block-filter__label--checkbox::after {
      top: 4px; }

.hi-radio {
  height: auto !important;
  line-height: initial !important; }

.hi-checkbox__input {
  background-color: #fff; }

.hi-tree .hi-tree-bar .hi-tree-checkbox {
  background-color: #fff; }

.checkboxs-group {
  display: flex; }
