.page--detail-basic {
  padding: 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box;
}

.detail-basic {
  &__header {
    // background: #e7e7e7;

    .row {
      &-01 {
        color: #999;

        .spacer {
          margin: 0 10px;
        }
      }

      &-02 {
        color: #383e47;
        margin-top: 14px;
        padding: 0 0 18px;
      }

      &-03 {
        padding-top: 4px;
        font-size: 12px;
        color: #999;

        .spacer {
          margin: 0 20px;

          &:last-child {
            display: none;
          }
        }
      }
    }
  }

  &__body {
    background: #fff;
    .title {
      font-size: 16px;

      &:not(:first-child) {
        margin-top: 12px;
      }
    }

    ul {
      list-style: none;
      padding: 0;
    }

    li {
      display: flex;
      margin-top: 8px;
      line-height: 22px;

      div {
        &:first-child {
          width: 110px;
          height: 100%;
          flex-shrink: 0;
          color: #999;
        }

        &:last-child {
          width: 68%;
        }
      }
    }
  }
}
